.form {
  @media (min-width: $desktop-width) {
    margin: 0 auto;
    padding: 0 110px 0 70px;
    width: $desktop-width;
    box-sizing: border-box;
    display: grid;
    grid-template-columns: repeat(2, 610px);
    column-gap: 40px;
    margin-bottom: 37px;
  }
}

.form__group {
  @include zero-indent;
  border: 0;
  margin-bottom: 23px;

  @media (min-width: $tablet-width) {
    margin-bottom: 70px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 47px;
  }
}

.form__group--line {
  position: relative;

  &::before {
    content: "";
    height: 2px;
    width: 100%;
    background-color: #68b738;
    position: absolute;
    top: 15px;
    left: 0;
    z-index: -1;

    @media (min-width: $tablet-width) {
      top: 22px;
    }

    @media (min-width: $desktop-width) {
      width: 200%;
      left: -50%;
    }
  }
}

.form__group--bottom-border {
  border-bottom: 1px solid #d9d9d9;
  padding-bottom: 23px;

  @media (min-width: $tablet-width) {
    border: none;
    padding: 0;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 64px;
  }
}

.form__group--row {
  @media (min-width: $tablet-width) {
    margin-bottom: 62px;
  }

  @media (min-width: $desktop-width) {
    grid-column: 1/-1;
  }
}

.form__group-wrapper {
  margin: 0 auto;
  width: $mobile-width;

  @media (min-width: $tablet-width) {
    width: $tablet-width;
  }

  @media (min-width: $desktop-width) {
    width: auto;
  }
}

.form__title {
  @include zero-indent;
  font-weight: 400;
  display: inline-block;
  padding-right: 18px;
  padding-bottom: 35px;
  padding-left: 20px;
  font-size: 24px;
  line-height: 1.08;
  color: #000000;
  background-color: #ffffff;
  position: relative;

  @media (min-width: $tablet-width) {
    br {
      content: "";
    }
    margin: 0 30px 76px;
    padding: 0 40px;
    font-size: 36px;
    line-height: 1;
    grid-column: 1/-1;
  }

  @media (min-width: $desktop-width) {
    margin: 0;
    margin-bottom: 75px;
  }
}

.form__legend--another-margin {
  @media (min-width: $tablet-width) {
    margin-bottom: 56px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 58px;
  }
}

.form__list {
  @include zero-indent;
  margin: 0 20px;
  list-style: none;

  @media (min-width: $tablet-width) {
    margin: 0 70px;
  }

  @media (min-width: $desktop-width) {
    margin: 0;
    padding-left: 40px;
  }
}

.form__list--full-border {
  @media (min-width: $tablet-width) {
    padding: 25px 28px 23px 25px;
    border: 1px solid #e7e7e7;
    margin-bottom: 5px;
  }

  @media (min-width: $desktop-width) {
    padding-bottom: 25px;
    margin-left: 40px;
  }
}

.form__list--full-border > .form__item {
  @media (min-width: $tablet-width) {
    margin-bottom: 13px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 13px;
  }
}

.form__list--checkbox-layout {
  @media (min-width: $tablet-width) {
    padding: 0;
    display: grid;
    grid-template-columns: repeat(2, 283px);
    grid-column-gap: 60px;
    grid-row-gap: 15px;
    margin-bottom: 36px;
  }

  @media (min-width: $desktop-width) {
    margin-left: 40px;
    margin-bottom: 42px;
    grid-template-columns: repeat(4, 221px);
    grid-column-gap: 104px;
  }
}

.form__list--owner-layout {
  @media (min-width: $desktop-width) {
    margin: 0;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(2, 610px);
    column-gap: 40px;
  }
}

.form__list--owner-layout > .form__item {
  @media (min-width: $desktop-width) {
    padding-left: 40px;
  }
}


.form__item {
  @include zero-indent;
  margin-bottom: 14px;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: $tablet-width) {
    margin-bottom: 18px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.form__label,
.form__input,
.form__textarea {
  font-size: 16px;
  line-height: 1.12;
  text-transform: uppercase;
  color: #444444;
  font-family: inherit;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 1.5;
  }
}

.form__label {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  position: relative;
  box-sizing: border-box;
}

.form__label--radio,
.form__label--checkbox {
  padding-left: 45px;
  height: 30px;

  @media (min-width: $tablet-width) {
    padding-left: 60px;
    height: 40px;
  }
}

// .form__label--checkbox {
//   @media (min-width: $desktop-width) {
//     width: 221px;
//   }
// }


.form__input {
  margin-left: 14px;
  padding: 13px 40px 12px 14px;
  border: 1px solid #e7e7e7;
  width: 100%;
  box-sizing: border-box;

  @media (min-width: $tablet-width) {
    padding: 11px 50px 11px 19px;
  }
}

.form__input--radio {
  display: none;

  &:checked + .form__mark--radio::before {
    @include input-before;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background-color: #68b738;

    @media (min-width: $tablet-width) {
      width: 18px;
      height: 18px;
    }
  }
}

.form__input--checkbox {
  display: none;

  &:checked + .form__mark--checkbox::before {
    @include input-before;
    width: 30px;
    height: 30px;
    background-image: url("../img/mark.svg");
    background-repeat: no-repeat;
    background-position: 6px 8px;

    @media (min-width: $tablet-width) {
      width: 40px;
      height: 40px;
      background-image: url("../img/mark-large.svg");
      background-position: 9px 11px;
    }
  }
}

.form__mark {
  position: absolute;
  top: 0;
  left: 0;
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
  box-sizing: border-box;

  @media (min-width: $tablet-width) {
    width: 40px;
    height: 40px;
  }
}

.form__mark--radio {
  border-radius: 50%;
}

.form__icon {
  position: absolute;
  right: 34px;
  transform: translateX(100%);

  @media (min-width: $tablet-width) {
    right: 38px;
    transform: translateX(100%);
  }
}

.form__textarea {
  margin: 0;
  padding: 10px;
  font-size: 16px;
  line-height: 1.12;
  text-align: center;
  resize: none;
  box-sizing: border-box;
  height: 150px;
  width: 280px;
  border: 1px solid #e7e7e7;

  @media (min-width: $tablet-width) {
    padding-left: 17px;
    font-size: 20px;
    line-height: 1.5;
    text-align: left;
    height: 174px;
    width: 631px;
  }

  @media (min-width: $desktop-width) {
    width: 1220px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 14px;
  }
}

.form__group-submit-wrapper {
  margin-top: 34px;

  @media (min-width: $tablet-width) {
    margin: 0 70px 11px;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 55px;
  }

  @media (min-width: $desktop-width) {
    margin: 0;
    grid-template-columns: repeat(2, 610px);
    column-gap: 40px;
  }
}

.form__button {
  @include button;
  width: 280px;
  box-sizing: border-box;
  margin: 0 20px 21px;
  padding: 10px;
  font-size: 16px;
  line-height: 1.25;

  @media (min-width: $tablet-width) {
    margin: 0;
    padding: 12px 10px;
    font-size: 20px;
    line-height: 26px;
    width: 282px;
  }

  @media (min-width: $desktop-width) {
    margin-left: 40px;
    width: 570px;
  }
}

.form__required-text {
  @include zero-indent;
  font-family: $font-arial;
  font-size: 14px;
  line-height: 114%;
  text-align: center;
  color: #000000;

  @media (min-width: $tablet-width) {
    font-size: 16px;
    line-height: 1;
    text-align: left;
    align-self: center;
  }

  @media (min-width: $desktop-width) {
    margin-left: 40px;
  }
}
