.social {
  @media (min-width: $tablet-width) {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    max-width: 250px;
  }

  @media (min-width: $desktop-width) {
    max-width: 500px;
  }
}

.social__list {
  margin: 0;
  padding: 22px 75px;
  list-style: none;
  display: flex;
  justify-content: space-between;

  @media (min-width: $tablet-width) {
    padding: 0;
  }
}

.social__item {
  display: flex;
  align-items: center;
  // отступ последнего элемента
  &:not(:last-child) {
    margin-right: 26px;
  }
}

.social__link {
  display: flex;
  text-decoration: none;
  color: #666666;

  &:hover {
    color: #68b738;
  }

  &:active {
    opacity: 0.3;
  }
}
