.program-type {
  margin: 0 auto 20px;
  width: $mobile-width;
  box-sizing: border-box;

  @media (min-width: $tablet-width) {
    width: $tablet-width;
    margin: 0 auto 45px;
  }

  @media (min-width: $desktop-width) {
    width: $desktop-width;
    margin: 0 auto 71px;
  }
}

.program-type__list {
  @include zero-indent;
  margin: 0 20px;
  list-style: none;

  @media (min-width: $tablet-width) {
    margin: 0 30px;
  }

  @media (min-width: $desktop-width) {
    margin: 0 110px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 80px;
  }
}

.program-type__item {
  background: #f2f2f2;
  padding: 30px 20px 19px 20px;
  position: relative;
  margin-bottom: 20px;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: $tablet-width) {
    margin-bottom: 30px;
    padding: 77px 316px 48px 53px;
  }

  @media (min-width: $desktop-width) {
    margin: 0;
    padding: 77px 51px 55px 52px;
  }
}

.program-type__logo {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 24px;
  left: 22px;
  width: 50px;
  height: 50px;
  background-color: #68b738;
  border-radius: 50%;

  @media (min-width: $tablet-width) {
    width: 200px;
    height: 200px;
    top: 91px;
    left: 446px;
  }

  @media (min-width: $desktop-width) {
    width: 100px;
    height: 100px;
    top: 47px;
    left: 52px;
  }
}

.program-type__svg {
  position: absolute;
}

.program-type__svg--loss {
  top: -3px;
  left: 7px;

  @media (min-width: $tablet-width) {
    width: 140px;
    height: 194px;
    top: -11px;
    left: 30px;
  }

  @media (min-width: $desktop-width) {
    width: 70px;
    height: 97px;
    top: -6px;
    left: 15px;
  }
}

.program-type__svg--gain {
  top: 11px;
  left: -10px;

  @media (min-width: $tablet-width) {
    width: 268px;
    height: 112px;
    top: 44px;
    left: -36px;
  }

  @media (min-width: $desktop-width) {
    width: 134px;
    height: 56px;
    top: 20px;
    left: -19px;
  }
}

.program-type__title {
  @include zero-indent;
  margin-left: 71px;
  margin-bottom: 30px;
  font-weight: 400;
  font-size: 24px;
  line-height: 154%;
  text-transform: uppercase;
  color: #000000;

  @media (min-width: $tablet-width) {
    margin: 0;
    margin-bottom: 24px;
    font-size: 36px;
    line-height: 1;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 73px;
    margin-left: 162px;
  }
}

.program-type__description {
  @include zero-indent;
  padding-right: 13px;
  margin-bottom: 37px;
  font-family: $font-arial;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.29;
  color: #444444;
  position: relative;

  @media (min-width: $tablet-width) {
    margin-bottom: 48px;
    font-size: 16px;
    line-height: 1.5;
    color: #000000;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 27px;
  }
}

.program-type__description::after {
  content: "";
  display: flex;
  width: 240px;
  height: 1px;
  background-color: #d9d9d9;
  position: absolute;
  bottom: -22px;

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.program-type__link {
  font-weight: 400;
  font-size: 16px;
  line-height: 100%;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  color: #000000;
  padding-right: 25px;
  position: relative;
  background-color: #f2f2f2;
  z-index: 1;

  &::before {
    content: "";
    position: absolute;
    right: -25px;
    top: 8px;
    width: 32px;
    height: 12px;
    background-image: url("../img/program-type-link-icon.svg");
    z-index: -1;
    transition: 1s;
  }

  &:hover {
    &::before {
      right: -31px;
    }
  }

  &:active {
    opacity: 0.3;
  }

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 150%;
  }
}

